.profile__avatar-edit-button {
  display: flex;
  position: relative;
  height: 120px;
  width: 120px;
  border-radius: 50%;
  margin: 0;
  padding: 0;
  object-position: center;
  background-color: transparent;
  border: none;
}

.profile__avatar-edit-button:before {
  content: "";
  background-image: url(../../../images/avatar__edit-button.svg);
  background-color: rgba(0, 0, 0, 0.8);
  background-size: 26px;
  background-position: center;
  cursor: pointer;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-repeat: no-repeat;
  opacity: 0;
}

.profile__avatar-edit-button:hover::before {
  cursor: pointer;
  opacity: 1;
}
